// Chaching needed Dom Elements
const colorsPanel = window.document.querySelector('.color-panel');
const colorsPanelSetting = window.document.querySelector(
  '.color-panel-setting'
);

// Helper Function
function pushThemeToLocalStorage(colorTheme) {
  localStorage.setItem('colorTheme', colorTheme);
}

function popThemeFromLocalStorage() {
  return localStorage.getItem('colorTheme');
}

