// handle click eventes on colors panel menu using event delegation.
colorsPanel.addEventListener('click', (e) => {
  // check if click on theme item
  if (e.target.classList.contains('theme-item')) {
    const colorThemeClass = e.target.getAttribute('data-color');
    document.documentElement.className = colorThemeClass;
    pushThemeToLocalStorage(colorThemeClass);
  }

  // check if click on colors Panel Setting Button
  if (e.target === colorsPanelSetting) {
    colorsPanel.classList.toggle('open');
  }
});

export const colorsPanelInit = () => {
  // Restore color From LocalStorage if exist .
  const defaultColorThemeClass = popThemeFromLocalStorage();
  if (defaultColorThemeClass)
    document.documentElement.className = defaultColorThemeClass;
};
